body {
  margin: 0;
  font-family: "Cerebri Sans", sans-serif;
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 1.5;
  color: #12263f;
  text-align: left;
  background-color: #f9fbfd;
}

.display-1,
.display-2,
.display-3,
.display-4 {
  letter-spacing: -0.02em;
}
.text-center {
  text-align: center !important;
}
.mb-3,
.my-3 {
  margin-bottom: 0.75rem !important;
}
.display-4 {
  font-size: 2rem;
  font-weight: 600;
  line-height: 1.1;
}

.h4,
h4 {
  font-size: 0.9375rem;
}

.text-muted {
  color: #95aac9 !important;
}
.text-center {
  text-align: center !important;
}
.mb-5,
.my-5 {
  margin-bottom: 2.25rem !important;
}

.mb-5,
.my-5 {
  margin-bottom: 2.25rem !important;
}
@media (min-width: 768px) {
  .col-md-5 {
    flex: 0 0 41.66667%;
    max-width: 41.66667%;
  }
}

.no-padding {
  padding: 0px !important;
}

.form-group {
  margin-bottom: 1.375rem;
}
label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

.form-control:not(.custom-select) {
  padding-right: 0.75rem;
}

.align-self-end {
  align-self: flex-end !important;
}

.dashboard-header {
  margin-bottom: 2rem;
}

.header-body {
  padding-top: 0rem;
  padding-bottom: 1.5rem;
  border-bottom: 1px solid #e3ebf6;
}

.card {
  margin-bottom: 1.5rem;
  border-color: #edf2f9;
  box-shadow: 0 0.75rem 1.5rem rgb(18 38 63 / 3%);
  border: 1px solid #edf2f9;
  border-radius: 0.5rem;
}

.card-header:first-child {
  border-radius: calc(0.5rem - 1px) calc(0.5rem - 1px) 0 0;
}

.card-header {
  padding: 1rem 1.5rem;
  margin-bottom: 0;
  background-color: transparent;
  border-bottom: 1px solid #edf2f9;
}

img {
  width: 100%;
  height: 100%;

  max-width: 60% !important;

  vertical-align: middle;
  height: auto;
  border-style: none;
}

.btn-list {
  margin: 0.5rem 0.5rem !important;
  min-width: 10em;
}

.btn-primary {
  color: #fff;
  background-color: #2c7be5;
  border-color: #2c7be5;
}

.btn-group-lg > .btn,
.btn-lg {
  padding: 0.75rem 1.25rem;
  font-size: 0.9375rem;
  line-height: 1.5;
  border-radius: 0.5rem;
}

.btn-selection {
  width: 10em !important;
  height: 4em !important;
  padding: 0.75rem !important;
}

.lift {
  transition: box-shadow 0.25s ease, transform 0.25s ease;
}

.lift:focus,
.lift:hover {
  box-shadow: 0 1rem 2.5rem rgba(18, 38, 63, 0.1),
    0 0.5rem 1rem -0.75rem rgba(18, 38, 63, 0.1) !important;
  transform: translate3d(0, -3px, 0);
}

.lift-lg:focus,
.lift-lg:hover {
  box-shadow: 0 2rem 5rem rgba(18, 38, 63, 0.1),
    0 0.5rem 1rem -0.75rem rgba(18, 38, 63, 0.05) !important;
  transform: translate3d(0, -5px, 0);
}

.form-control::-webkit-input-placeholder {
  color: #b1c2d9;
  opacity: 1;
}

.form-control::-moz-placeholder {
  color: #b1c2d9;
  opacity: 1;
}

.form-control:-ms-input-placeholder {
  color: #b1c2d9;
  opacity: 1;
}

.form-control::-ms-input-placeholder {
  color: #b1c2d9;
  opacity: 1;
}

.form-control::placeholder {
  color: #b1c2d9;
  opacity: 1;
}
